


.controls{
    position: relative;
    width: 50vw;
    max-height: 200px;
    height: 15vh;
    margin-left: 0vw;
    margin-top: -15vh;
    z-index: 99999;
    text-align: center;
    padding: 10px;
}

.intend {
    position: relative;
    width: 50vw;
    max-height: 200px;
    height: 20vh;
    margin-left: 50vw;
    margin-top: -45vh;
    z-index: 99999;
    text-align: center;
    padding: 10px;
}

.dialog{
    background-color: #faebd7;
    font-size: 24px;
    padding: 10px;
    border-radius: 16px;
    box-shadow: -3px 3px 0 #000;
}

#changeStatus{
    margin-top: 10px;
    transition: all .15s linear 0s;
    display: inline-block;
    padding: 15px 25px;
    background-color:#faebd7;
    
    color:#000;
    font-weight:800;
    font-family: arial;
    letter-spacing: 1px;

    box-shadow: -3px 3px 0 #000;
    text-decoration: none;
    border-radius: 16px;
}

